@import '../../styles/_colors.scss';
@import '../../styles/_mixins.scss';

$numberPadsHeight: 5rem;
$fontPadSize: 2rem;

.Calculator {
    font-size: 10px;
    border: #ccc solid 5px;
    background-color: $portfolio-gray-blue;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 4rem;
    padding-bottom: 2rem;
    @include shadow;
    &__display {
        display: grid; 
        grid-template-columns: repeat(5, $numberPadsHeight);
        grid-template-rows: repeat(4, $numberPadsHeight);
        grid-gap: 5px;

    }
    &__numbers {
        grid-column: 1 / span 3;
        display: grid;
        grid-template-columns: repeat(3, $numberPadsHeight);
        grid-template-rows: repeat(4, $numberPadsHeight);
        grid-gap: 5px;
    }
    &__button {
        @include shadow;
        font-size: $fontPadSize;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10px;
        color: $portfolio-white;
        background-color: $portfolio-dark-blue;
    }
    &__zero {
        grid-column: span 2 / 3;
    }

    &__minus {
        grid-row: 2;
    }

    &__operators {
        display: grid;
        grid-gap: 5px;
        grid-column: 4 / span 1;
        grid-row: 1 / span 1;
        grid-template-columns: repeat(2, $numberPadsHeight);
        grid-template-rows: repeat(4, $numberPadsHeight);
        &--border-right {
            grid-column: 2;
        }
    }

    &__AC {
        grid-row: span 2;
    }

    &__equal {
        grid-row: 3 / span 2;
    }

    &__input {
        width: 100%;
        height: $numberPadsHeight;
        margin-bottom: 1rem;
        font-size: $fontPadSize;
        padding-right: 1rem;
        text-align: right;
    }

}

